import React from 'react';
import {useHistory} from "react-router";

const ConfigPage = () => {

    const router = useHistory();

    return (
        <div>
            <ul>
                <li>
                    <button onClick={() => {router.push('/logout')}}>Выйти из аккаунта</button>
                </li>
            </ul>
        </div>
    );
};

export default ConfigPage;