import React from 'react';
import AuthService from "../../services/auth.service";
import {UserContext} from "../../context";

const Logout = () => {

    const {user, setUser} = React.useContext(UserContext);

    React.useEffect(() => {

        AuthService.logout();
        setUser(false);

    }, []);

    return (
        <div>

        </div>
    );
};

export default Logout;